      <p className={s.bold}>2. <span>–î–æ—Ä–æ—à–∏–Ω –ò–≤–∞–Ω –ê–ª–µ–∫—Å–∞–Ω–¥—Ä–æ–≤–∏—á,</span> –∫–∞–Ω–¥–∏–¥–∞—Ç –∏—Å—Ç–æ—Ä–∏—á–µ—Å–∫–∏—Ö –Ω–∞—É–∫, –¥–æ—Ü–µ–Ω—Ç –°–∞—Ä–∞—Ç–æ–≤—Å–∫–æ–≥–æ –≥–æ—Å—É–¥–∞—Ä—Å—Ç–≤–µ–Ω–Ω–æ–≥–æ —É–Ω–∏–≤–µ—Ä—Å–∏—Ç–µ—Ç–∞ –∏–º. –ù.–ì. –ß–µ—Ä–Ω—ã—à–µ–≤—Å–∫–æ–≥–æ;</p>
