const ADD_POST = 'ADD-POST';
const UPDATE_NEW_POST_TEXT = 'UPDATE-NEW-POST-TEXT';
const ADD_QUESTION = 'ADD-QUESTION';
const UPDATE_NEW_QUESTION_TEXT = 'UPDATE-NEW-QUESTION-TEXT';

let initialState = {
    addQuestionDate: [
        {id: 1, message: 'Hi, how are you?', likesCount: 12},
        {id: 2, message: 'It\'s my first post', likesCount: 11},
        {id: 3, message: 'Blabla', likesCount: 11},
        {id: 4, message: 'Dada', likesCount: 11}
    ],
    addQuestionDate2: [
        {id: 1, message: '–ö–∞–∫ –¥–µ–ª–∞', likesCount: 12},
        {id: 2, message: '–ü–æ—á–µ–º –Ω—ã–Ω—á–µ –≤–µ—Ä—Å—Ç–∫–∞', likesCount: 11},
        {id: 3, message: '–ì–¥–µ –±–µ–∂–µ–Ω—Ü—ã', likesCount: 11},
        {id: 4, message: '–°–∫–æ–ª—å–∫–æ –≤–µ—Å–∏—Ç –∫–≥ —Ö–ª–æ–ø–∫–∞?', likesCount: 11}
    ],
    newPostText: 'saristoch.ru',
    newQuestionText: '–ù–µ—É–∂–µ–ª–∏ –∑–∞–±—ã–ª–∏ –≤–æ–ø—Ä–æ—Å',
};

const questionsReducer = (state = initialState, action) => {
    switch(action.type) {
        case ADD_POST: {
            let newPost = {
                id: 5, 
                message: state.newPostText, 
                likesCount: 0,
            };
            return {
                ...state,
                addQuestionDate: [...state.addQuestionDate, newPost],
                newPostText: '',
            };
        }
        case ADD_QUESTION: {
            let newQuestion = {
                id: 5, 
                message: state.newQuestionText, 
                likesCount: 0,
            };
            return {
                ...state,
                addQuestionDate2: [...state.addQuestionDate2, newQuestion],
                newQuestionText: '',
            };
        }
        case UPDATE_NEW_POST_TEXT: {
            return {
                ...state,
                newPostText: action.newText,
            };
        }
        case UPDATE_NEW_QUESTION_TEXT: {
            return {
                ...state,
                newQuestionText: action.newText,
            };
        }
        default:
            return state;
    }
}

export const addPostActionCreator = () => ({type: ADD_POST,})
export const updateNewPostTextActionCreator = (text) => ({
    type: UPDATE_NEW_POST_TEXT, newText: text,})

export const addQuestionActionCreator = () => ({type: ADD_QUESTION,})
export const updateNewQuestionTextActionCreator = (text) => ({
    type: UPDATE_NEW_QUESTION_TEXT, newText: text,})

export default questionsReducer;